<template>
  <div class="adminOrder">
    <el-container>
      <el-aside style="width: 290px;  text-align:left;">
        <div style="display: block; position: fixed; left: 0px;width: 290px; height:100%">
          <div style="">
            <el-menu
              class="el-menu-demo"
              mode="horizontal"
              text-color="#FFFFFF"
              background-color="#409EFF"
              collapse-transition
              active-text-color="#ffd04b">
              <el-menu-item index="1">Logo</el-menu-item>
              <el-menu-item index="2"></el-menu-item>
              <el-menu-item index="3"><router-link to="/" style="text-decoration: none"></router-link></el-menu-item>
            </el-menu>
          </div>

          <div style="height:100%">
            <el-menu default-active="2" class="el-menu-vertical-demo" >
              <router-link to="/admin" style="text-decoration: none">
                <el-menu-item index="1">
                  <i class="el-icon-menu"></i>
                  <span slot="title" style="align:left"></span>
                </el-menu-item>
              </router-link>
              <router-link to="/adminOrder" style="text-decoration: none">
                <el-menu-item index="2">
                  <i class="el-icon-setting"></i>
                  <span slot="title"></span>
                </el-menu-item>
              </router-link>
              <router-link to="/activityEdit" style="text-decoration: none">
                <el-menu-item index="3">
                  <i class="el-icon-document"></i>
                  <span slot="title"></span>
                </el-menu-item>
              </router-link>

            </el-menu>

          </div>
        </div>
      </el-aside>

      <el-main>
        <div>
          <div style="margin-top:20px;">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
          </div>

          <!---->
          <div style="margin-top: 40px; padding:16px; background-color: #E9EEF3; text-align: left;">
            <el-form :inline="true" :model="formInline" label-width="80px" style="height:100px" >
              <el-form-item label=":" style="width:300px">
                <el-input v-model="formInline.stuID" ></el-input>
              </el-form-item>
              <el-form-item label=":" style="width:500px">
                <el-date-picker
                  v-model="formInline.timeValue"
                  type="datetimerange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  start-placeholder=""
                  end-placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" style="width:600px">
                <template>
                  <el-checkbox-group v-model="checkList">
                    <el-checkbox label="" border></el-checkbox>
                    <el-checkbox label="" border></el-checkbox>
                    <el-checkbox label="" border></el-checkbox>
                    <el-checkbox label="" border></el-checkbox>
                  </el-checkbox-group>
                </template>
              </el-form-item>
              <el-form-item style="width:160px;float: right" >
                <el-button type="primary" size="small" icon="el-icon-search"
                           @click="searchByName(formInline.stuID,formInline.timeValue)"></el-button>
                <el-button type="success" size="small" icon="el-icon-document" @click="pullOut"></el-button>
              </el-form-item>
            </el-form>
          </div>

          <!---->
          <div style="margin-top: 40px">
            <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" max-height="500" border style="width: 100%" >
              <el-table-column prop="time" label="" width="200px" sortable></el-table-column>
              <el-table-column prop="id" label="" width="100px" sortable></el-table-column>
              <el-table-column prop="studentID" label="" width="150px" sortable></el-table-column>
              <el-table-column prop="name" label="" width="130px" sortable></el-table-column>
              <el-table-column prop="money" label="" width="130px" sortable></el-table-column>
              <el-table-column prop="status" label="" width="130px" sortable
                               :filters="[{ text: '', value: '' }, { text: '', value: '' },
                                          { text: '', value: '' }, { text: '', value: '' },]"
                               :filter-method="filterTag">
                <template slot-scope="scope">
                  <el-tag
                           :type="scope.row.statusType"
                           disable-transitions>{{scope.row.status}}
                  </el-tag>
                </template>
              </el-table-column>
              <el-table-column label=""  fixed="right" width="150px">
                <template slot-scope="scope">
                  <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                  <el-button type="primary" size="small" @click="lookMore(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>

            <div style="margin-top: 20px; ">
              <el-pagination
                :total="totalSize"
                @current-change="currentChange"
                :page-size="pageSize"
                layout="prev, pager, next, jumper">
              </el-pagination>
            </div>
          </div>

          <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
            <el-form :model="form">
              <el-form-item label="" :label-width="formLabelWidth">
                <el-select v-model="form.formStatus" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="confirmEdit"> </el-button>
              <el-button @click="dialogFormVisible = false"> </el-button>
            </div>
          </el-dialog>

          <el-dialog title="" :visible.sync="lookFormVisible" width="40%">
            <el-form :model="lookform">
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookName" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookTel" auto-complete="off" ></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookPosition" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookAddress" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookArriveTime" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookGoods" type="textarea" :rows="4" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookTime" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookStatus" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="lookform.lookMoney" auto-complete="off"></el-input>
              </el-form-item>

            </el-form>
            <!--<div slot="footer" class="dialog-footer">-->
              <!--<el-button type="primary" @click="confirmL"> </el-button>-->
              <!--<el-button @click="newdialogFormVisible = false"> </el-button>-->
            <!--</div>-->
          </el-dialog>

        </div>

      </el-main>
    </el-container>

  </div>
</template>

<script>
  const SecretNumber="123456";

  import app from "../App"
  export default {
    name: "adminOrder",
    data() {
      return {
        //
        tableData:[{
          time:'1',
          id:'1',
          studentID:'1',
          name:'1',
          money:'1',
          statusType:'warning',
          status:''
        }],

        //
        formInline: {
          stuID: '',
          timeValue:[],
        },
        checkList: [],

        //
        form: {
          formStatus:'',
        },

        //
        lookform: {
          lookName:'',
          lookTel:'',
          lookPosition:'',
          lookAddress:'',
          lookArriveTime:'',
          lookGoods:'',
          lookTime:'',
          lookStatus:'',
          lookMoney:''
        },
        formLabelWidth: '120px',

        //Index
        currentIndex:'',

        //
        selectStatus:[],
        //
        isLoad:"0",

        //
        dialogFormVisible: false,
        //
        lookFormVisible: false,

        //
        pageSize:7,
        totalSize:100,
        currentPage:1,//
      }
    },

    created(){
        this.loadAllUsers();
    },

    methods: {

      //
      filterTag(value, row) {
        let _this=this;
        _this.clearSelectStatus();
        var have="0";//value
        for(var i=0;i<_this.$data.selectStatus.length;i++){
          if(_this.$data.selectStatus[i]===value){have="1"; break;}
        }
        if(have==="0"){_this.$data.selectStatus.push(value);}
        return row.status === value;
      },


      //
      loadAllUsers:function() {
        let _this=this;

        this.$axios({
          method:'get',
          url:'/order/getOrderList',

        }).then(function (response) {
          console.log(response.data);
          _this.$data.tableData = [];
          var types='';
          var status='';
          for(var i=0;i<response.data.length;i++){
            if(response.data[i].status===1){
              status='';
              types='danger';
            }
            if(response.data[i].status===0){
              status='';
              types='info';
            }
            if(response.data[i].status===2){
              status='';
              types='primary'
            }
            if(response.data[i].status===3){
              status='';
              types='success';
            }
            var newItem={time:response.data[i].reservetime,id:response.data[i].orderid,studentID:response.data[i].studentid,name:response.data[i].name,
                          money:parseFloat(response.data[i].deposit),status:status,statusType:types};
            _this.$data.tableData.push(newItem);
          }

          _this.$data.totalSize=parseInt(response.data.length);//

        }).catch(function (error) {
          console.log(error.response.data);
          console.log('error on loadAllOrders');
        })
      },

      //
      handleEdit:function (index, row){
        let _this=this;
        _this.$data.currentIndex=index*(_this.$data.currentPage-1)+_this.$data.pageSize;//Index

        //
        if(row.status===""){
          this.$message({
            message: 'Sorry , ',
            type: 'warning'
          });
        }
        else{
          _this.$data.form.formStatus=row.status;
          _this.$data.dialogFormVisible = true;
        }
      },

      //
      confirmEdit:function (){
        let _this=this;
        this.$confirm(' '+_this.$data.form.formStatus+' ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var id=_this.$data.tableData[_this.$data.currentIndex].id;
          var studentid=_this.$data.tableData[_this.$data.currentIndex].studentID;
          var newStatus=0;
          if(_this.$data.form.formStatus===''){newStatus=3;}
          else if(_this.$data.form.formStatus===''){newStatus=2;}
          else if(_this.$data.form.formStatus===''){newStatus=1;}
          else if(_this.$data.form.formStatus===''){newStatus=0;}

          this.$axios({
            method:'get',
            url:'/order/changeOrderStatus?studentid='+studentid+'&orderid='+id+'&status='+parseInt(newStatus)

          }).then(function (response) {
            console.log(response.data);
            if(response.data.toString()==="true"){
              _this.$data.tableData[_this.$data.currentIndex].status=_this.$data.form.formStatus;
              if(newStatus===1){_this.$data.tableData[_this.$data.currentIndex].statusType="danger";}
              else if(newStatus===2){_this.$data.tableData[_this.$data.currentIndex].statusType="primary";}
              else if(newStatus===3){_this.$data.tableData[_this.$data.currentIndex].statusType="success";}
              else if(newStatus===0){_this.$data.tableData[_this.$data.currentIndex].statusType="info";}
            }
            _this.$data.dialogFormVisible = false;
          }).catch(function (error) {
            console.log(error.response.data)
          });

          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },

      //
      searchByName:function(stuID,time){
        let _this=this;
        if(stuID===""){stuID="null"}//null

        //null
        if(time==null){
          time=["null","null"];
        }
        if(typeof(time[0]) == "undefined"){time[0]="null";}
        if(typeof(time[1]) == "undefined"){time[1]="null";}

        //
        var statusArray=_this.$data.checkList;
        var responseArray="";
        if(statusArray.length==0){responseArray=",-1";}
        else{
          for(var i=0;i<statusArray.length;i++){
            if(statusArray[i].toString()===""){responseArray+=",3"}
            if(statusArray[i].toString()===""){responseArray+=",2"}
            if(statusArray[i].toString()===""){responseArray+=",1"}
            if(statusArray[i].toString()===""){responseArray+=",0"}
          }
        }
        console.log(responseArray);

        this.$axios({
          method:'get',
          url:'order/getSiftOrder?statu='+responseArray+'&studentid='+stuID+'&firstTime='+time[0]+'&secondTime='+time[1],

        }).then(function (response) {
          console.log(response.data);
          _this.$data.tableData = [];
          var types='';
          var status='';
          for(var i=0;i<response.data.length;i++){
            if(response.data[i].status===1){
              status='';
              types='danger';
            }
            if(response.data[i].status===0){
              status='';
              types='info';
            }
            if(response.data[i].status===2){
              status='';
              types='primary'
            }
            if(response.data[i].status===3){
              status='';
              types='success';
            }
            var newItem={time:response.data[i].reservetime,id:response.data[i].orderid,studentID:response.data[i].studentid,name:response.data[i].name,
              money:parseFloat(response.data[i].deposit),status:status,statusType:types};
            _this.$data.tableData.push(newItem);
          }
          _this.$data.totalSize=_this.$data.tableData.length;//totalSize
        }).catch(function (error) {
          console.log(error.response.data);
          console.log('error on search by id and time');
        })
      },

      //
      lookMore:function (index, row){
        let _this=this;
        _this.$data.lookFormVisible = true;
        _this.$data.currentIndex=index*(_this.$data.currentPage-1)+_this.$data.pageSize;

        this.$axios({
          method:'get',
          url:'/order/getOrderMore?studentid='+row.studentID+'&orderid='+parseInt(row.id),

        }).then(function (response) {
          console.log(response.data);
          var goods=response.data.goods.toString();
          var newGoods=goods.replace(/\/n/g, "\r\n");
          _this.$data.lookform.lookName=response.data.name;
          _this.$data.lookform.lookTel=response.data.phone;
          _this.$data.lookform.lookPosition=response.data.campus;
          _this.$data.lookform.lookAddress=response.data.position;
          _this.$data.lookform.lookArriveTime=response.data.arrivedate;
          _this.$data.lookform.lookGoods=newGoods;
          _this.$data.lookform.lookTime=response.data.reservetime;
          _this.$data.lookform.lookMoney=response.data.deposit;
          if(response.data.status===0){_this.$data.lookform.lookStatus='';}
          else if(response.data.status===1){_this.$data.lookform.lookStatus='';}
          else if(response.data.status===2){_this.$data.lookform.lookStatus='';}
          else if(response.data.status===3){_this.$data.lookform.lookStatus='';}
          else {_this.$data.lookform.lookStatus='';}
        }).catch(function (error) {
          _this.$data.lookform={};
          console.log(error.response.data);
        })

      },

      //
      currentChange:function(currentPage){
        this.currentPage = currentPage;
      },

      //excel
      pullOut:function () {
        let _this=this;

        var tableData=_this.$data.tableData;
        var orderIDArray=[];
        for(var i=0;i<tableData.length;i++){
          orderIDArray.push(tableData[i].id);
        }
        console.log(orderIDArray);

        this.$axios({
          method:'get',
          url:'/order/newExcl?orderid='+orderIDArray,
        }).then(function (response) {
          //console.log(response.data);
          let responseURL=response.data.toString();
          console.log(responseURL);
          //
          window.location.href=responseURL;
        }).catch(function (error) {
          console.log(error.response.data);
          console.log('error on loadAllOrders');
        })


        // this.$axios({
        //   method:'get',
        //   url:'/order/newExcl?file='+path,
        // }).then(function (response) {
        //   console.log(response.data);
        // }).catch(function (error) {
        //   console.log(error.response.data)
        //   console.log('error on loadAllOrders');
        // })
      },
    }
  }
</script>

<style scoped>
  .adminOrder{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>



