<template>
  <div class="admin">
    <el-container>
      <el-aside style="width: 290px;  text-align:left;">
        <div style="display: block; position: fixed; left: 0px;width: 290px; height:100%">
          <div style="">
            <el-menu
              class="el-menu-demo"
              mode="horizontal"
              text-color="#FFFFFF"
              background-color="#409EFF"
              collapse-transition
              active-text-color="#ffd04b">
              <el-menu-item index="1">Logo</el-menu-item>
              <el-menu-item index="2"></el-menu-item>
              <el-menu-item index="3"><router-link to="/" style="text-decoration: none"></router-link></el-menu-item>
            </el-menu>
          </div>

          <div style="height:100%">
            <el-menu default-active="1" class="el-menu-vertical-demo">
              <router-link to="/admin" style="text-decoration: none">
                <el-menu-item index="1">
                  <i class="el-icon-menu"></i>
                  <span slot="title" style="align:left"></span>
                </el-menu-item>
              </router-link>
              <router-link to="/adminOrder" style="text-decoration: none">
                <el-menu-item index="2">
                  <i class="el-icon-setting"></i>
                  <span slot="title"></span>
                </el-menu-item>
              </router-link>
              <router-link to="/activityEdit" style="text-decoration: none">
                <el-menu-item index="3">
                  <i class="el-icon-document"></i>
                  <span slot="title"></span>
                </el-menu-item>
              </router-link>
            </el-menu>

          </div>
        </div>
      </el-aside>

      <el-main>
        <div>
          <div style="margin-top:20px;">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
          </div>

          <!---->
          <div style="margin-top: 40px; padding:16px; background-color: #E9EEF3; text-align: left;">
            <el-form :inline="true" :model="formInline" label-width="100px" style="height:45px" >
              <el-form-item label=":" style="width:300px">
                <el-input v-model="formInline.name" size="small" ></el-input>
              </el-form-item>
              <el-form-item style="width:100px">
                <el-button type="primary" size="small" @click="searchByName(formInline.name)"></el-button>
              </el-form-item>
              <el-form-item style="width:100px;float: right" >
                <el-button type="success"  size="small" icon="el-icon-plus" @click="handleNew"></el-button>
              </el-form-item>
            </el-form>
          </div>

          <!---->
          <div style="margin-top: 40px">
            <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"  max-height="500" fit border stripe style="width: 100%">
              <el-table-column prop="id" label="ID" width="100px" sortable></el-table-column>
              <el-table-column prop="name" label="" sortable></el-table-column>
              <el-table-column prop="picture" label="" >
                <template slot-scope="scope" >
                  <img :src="scope.row.picture" style="height: 80px;width: 120px"/>
                </template>
              </el-table-column>
              <el-table-column prop="price" label="" sortable></el-table-column>
              <el-table-column prop="text" label=""></el-table-column>
              <el-table-column label="" >
                <template slot-scope="scope">
                  <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                  <el-button type="danger" size="small" @click="confirmDelete(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>

            <div style="margin-top: 20px; ">
              <el-pagination
                :total="totalSize"
                @current-change="currentChange"
                :page-size="pageSize"
                layout="prev, pager, next, jumper">
              </el-pagination>
            </div>
          </div>

          <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
            <el-form :model="form">
              <el-form-item label="ID" :label-width="formLabelWidth">
                <el-input v-model="form.formID" auto-complete="off" disabled></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.formName" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.formPrice" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="form.formText" type="textarea" :rows="4" auto-complete="off"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="confirmEdit"> </el-button>
              <el-button @click="dialogFormVisible = false"> </el-button>
            </div>
          </el-dialog>

          <el-dialog title="" :visible.sync="newdialogFormVisible" width="40%">
            <el-form :model="newform">
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="newform.newName" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-upload
                  class="upload-demo"
                  action="http://172.27.65.52:8686/goods/goodsPicture"
                  :data="pictureData"
                  ref="upload"
                  limit=1
                  :on-change="handleChange"
                  :on-remove="backChange"
                  :on-error="errorChange"
                  :auto-upload="false"
                  list-type="picture-card">
                  <el-button size="small" type="primary"></el-button>
                  <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                </el-upload>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="newform.newPrice" auto-complete="off" ></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-input v-model="newform.newText" type="textarea" :rows="4" auto-complete="off"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="confirmNew"> </el-button>
              <el-button @click="newdialogFormVisible = false"> </el-button>
            </div>
          </el-dialog>

        </div>

      </el-main>
    </el-container>


  </div>
</template>

<script>
  const SecretNumber="why123456";
  import app from "../App";

  export default {
    name: "admin",
    data() {
      return {
        //
        tableData:[{
          id:'',
          name:'',
          picture:'',
          price:'',
          text:'',
        }],

        //
        formInline: {
          name:''
        },

        //
        dialogFormVisible: false,
        //
        newdialogFormVisible: false,

        //
        form: {
          formName:'',
          formPrice:'',
          formText:'',
        },
        formLabelWidth: '120px',

        //
        newform: {
          newName:'',
          newPrice:'',
          newText:'',
        },

        //Index
        currentIndex:'',

        //
        pictureData:{name:''},

        //
        pageSize:4,
        totalSize:100,
        currentPage:1,//
      }
    },

    created(){
      if(app.methods.getCookie("IsAdminister")==="")
        this.CheckSecretNumber();
      else
        this.loadAllUsers();
    },

    methods: {

       //
      loadAllUsers:function() {
        let _this=this;

        this.$axios({
          method:'get',
          url:'/goods/getGoodsList',

        }).then(function (response) {
          console.log(response.data);
          _this.$data.tableData = [];
          for(var i=0;i<response.data.length;i++){
            var newItem={id:response.data[i].goodsid,name:response.data[i].name,picture:response.data[i].photo,
              price:parseFloat(response.data[i].price),text:response.data[i].text};
            _this.$data.tableData.push(newItem);
          }
          _this.$data.totalSize=parseInt(response.data.length);
        }).catch(function (error) {
          console.log(error.response.data)
        })
      },

      //
      confirmDelete:function(index, row){
        let _this=this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let newIndex=index*(_this.$data.currentPage-1)+_this.$data.pageSize;//Index
          this.handleDelete(newIndex,row);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },

      //
      handleDelete:function (index, row) {
        var name=row.name.toString();
        let _this=this;

        this.$axios({
          method:'get',
          url:'/goods/deleteItem?name='+name,
          contentType:'application/json;charset=UTF-8',

        }).then(function (response) {
          _this.tableData.splice(index,1);
        }).catch(function (error) {
          console.log(error.response.data)
        })
      },

      //
      handleEdit:function (index, row){
        let _this=this;
        _this.$data.dialogFormVisible = true;
        _this.$data.currentIndex=index*(_this.$data.currentPage-1)+_this.$data.pageSize;//Index
        _this.$data.form.formID=row.id;
        _this.$data.form.formName=row.name;
        _this.$data.form.formPrice=row.price;
        _this.$data.form.formText=row.text;
      },

      //
      confirmEdit:function (){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let _this=this;

          var id=_this.$data.form.formID;
          var name=_this.$data.form.formName;
          var price=_this.$data.form.formPrice;
          var text=_this.$data.form.formText;

          this.$axios({
            method:'get',
            url:'/goods/editItem?strid='+id+'&name='+name+'&price='+price+'&text='+text

          }).then(function (response) {
            console.log(response.data);
            let currentIndex=_this.$data.currentIndex;
            _this.$data.dialogFormVisible = false;
            if(response.data.toString()==='1'){
              _this.$data.tableData[currentIndex].name=_this.$data.form.formName;
              _this.$data.tableData[currentIndex].price=parseFloat(_this.$data.form.formPrice);
              _this.$data.tableData[currentIndex].text=_this.$data.form.formText;
            }
          }).catch(function (error) {
            console.log(error.response.data)
          });

          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },

      //
      handleNew:function (){
        let _this=this;
        _this.$data.newdialogFormVisible = true;
      },

      //
      confirmNew:function (){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let _this=this;

          var name=_this.$data.newform.newName;
          var price=_this.$data.newform.newPrice.toString();
          var text=_this.$data.newform.newText;

          this.$axios({
            method:'get',
            url:'/goods/addGoods?name='+name+'&price='+price+'&text='+text

          }).then(function (response) {
            console.log(response.data);
            var newItem={name:name,picture:'Not Completed',price:price,text:parseFloat(text)};
            _this.$data.tableData.unshift(newItem);//

            //id,,ID
            var res=response.data.toString();
            var type="^[0-9]*[1-9][0-9]*$";
            var r=new RegExp(type);
            var flag=r.test(res);
            if(flag){
              _this.pictureData.name=res;
              _this.$refs.upload.submit();//id
            }
            _this.$data.newdialogFormVisible = false;//
          }).catch(function (error) {
            console.log(error.response.data)
          });

          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },

      //
      searchByName:function(name){
        let _this=this;

        this.$axios({
          method:'get',
          url:'/goods/selectItemByName?name='+name,

        }).then(function (response) {
          //_this.$data.tableData=response.data;
          console.log(response.data);
          _this.$data.tableData = [];
          for(var i=0;i<response.data.length;i++){
            var newItem={id:response.data[i].goodsid,name:response.data[i].name,picture:'Not Completed',price:parseInt(response.data[i].price),text:response.data[i].text};
            _this.$data.tableData.push(newItem);
            _this.$data.totalSize=_this.$data.tableData.length;//totalSize
          }
        }).catch(function (error) {
          console.log(error.response.data)
        })

        // this.$axios({
        //   method:'post',
        //   url:'/goods/selectItemByName',
        //   data:{
        //     name:name
        //   }
        // }).then(function (response) {
        //   console.log(response.data);
        //   _this.$data.tableData = [];
        //   for(var i=0;i<response.data.length;i++){
        //     var newItem={name:response.data[i].name,picture:'Not Completed',price:parseInt(response.data[i].price),text:response.data[i].text};
        //     _this.$data.tableData.push(newItem);
        //   }
        // }).catch(function (error) {
        //   console.log(error.response.data)
        // })

      },

      //
      CheckSecretNumber(){
        let _this=this;
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          if(value===SecretNumber)
          {
            _this.$message({
              type: 'success',
              message: '',
            });

            _this.IsLogin=true;
            _this.loadAllUsers();
            app.methods.addCookie("IsAdminister","true",0);
          }
          else
          {
            _this.$message({
              type: 'error',
              message: '',
            });
            _this.CheckSecretNumber();
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });

          window.location.assign("http://localhost:8081/#/")
        });
      },

      //
      handleChange(event,file, fileList) {
        var divset = document.getElementsByClassName("el-upload el-upload--picture-card");
        for (var i = 0; i<divset.length;i++) {
          divset[i].style.display="none";
        };
      },

      //
      backChange(file, fileList) {
        var divset = document.getElementsByClassName("el-upload el-upload--picture-card");
        for (var i = 0; i<divset.length;i++) {
          divset[i].style.display="block";
          divset[i].style.margin="auto";
        };
      },

      //
      errorChange(err,file, fileList) {
        var divset = document.getElementsByClassName("el-upload el-upload--picture-card");
        for (var i = 0; i<divset.length;i++) {
          divset[i].style.display="block";
          divset[i].style.margin="auto";
        };
      },

      //YYYY-MM-DD HH:MM:SS
      getNowFormatDate() {
        let _this=this
        var date = new Date();
        var seperator1 = "-";
        var seperator2 = ":";
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
          + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
        _this.$data.pictureData.name=currentdate;
      },

      //
      currentChange:function(currentPage){
        this.currentPage = currentPage;
      }
    }

  }
</script>

<style scoped>
  .admin{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>



