<template>
  <div>
    <script :id=id type="text/plain"></script>
  </div>
</template>
<script>

  export default {
    name: 'UE',
    data () {
      return {
        editor: null
      }
    },
    props: {
      defaultMsg: {
        type: String
      },
      config: {
        type: Object,
      },
      id: {
        type: String
      },
    },
    mounted() {
      const _this = this;
      this.editor = UE.getEditor(this.id, this.config); // UE
      this.editor.addListener("ready", function () {
        _this.editor.setContent(_this.defaultMsg); // UE
      });
      console.log("");
    },
    methods: {
      getUEContent() { // 
        return this.editor.getContent()
      },
      getUEContentTxt() { // 
        return this.editor.getContentTxt()
      }
    },
    destroyed() {
      this.editor.destroy();
    }
  }
</script>



